'use strict';


import React from 'react';
import $ from 'jquery';
import cookie from 'react-cookie';
import uuid from 'lib/math';

var LinkBox = React.createClass({
    loadLinks: function(name) {
        return $.get('/api/links/' + name);
    },
    sendLink: function(link) {
        return $.post('/api/links', {link: link});
    },
    getInitialState: function() {
        var userId = cookie.load('userId') || '';
        if(!userId.length) {
            userId = uuid(32);
            cookie.save('userId', userId);
        }

        return ({links: [], userId: userId});
    },
    componentWillMount: function() {
        this.loadLinks();
    },
    render: function() {
        return (
            <div className="linkBox row">
                <div className="large-12 columns">
                    <LinkForm onLinkSubmit={this.sendLink} />
                    <LinkList data={this.state.links} />
                </div>
            </div>
        );
    }
});

var LinkForm = React.createClass({
    onLinkSubmit: function() {
        var link = this.refs.link.getDOMNode().value.trim();
        this.props.onLinkSubmit(link);
        this.refs.name.getDOMNode().value = '';
    },
    render: function() {
        return (
            <form className="linkForm row form" onSubmit={this.onLinkSubmit}>
                <input ref="link" type="text" placeholder="Paste your link here" />
            </form>
        );
    }
});

var LinkList = React.createClass({
    updateStatus: function() {

    },
    render: function() {
        if(!this.props.data.length) {
            return (
                <div className="noLinks">
                    There is no link available
                </div>
            );
        }
        var nodes = this.props.data.map(function(link, index) {
            return <LinkItem key={index} data={link.data} status={link.status} />
        });
        return (
            <div className="linkList">
                <h2>Links</h2>
                {nodes}
            </div>
        );
    }
});

var LinkItem = React.createClass({
    render: function() {
        switch (this.props.status) {
            case 'PENDING':
                return (
                    <div className="pendingLink">
                        {this.props.data.name}
                    </div>
                );
                break;
            case 'DOWNLOADING':
                return (
                    <div className="dowloadingItem">
                        {this.props.data.name} - {this.props.data.percent} %
                    </div>
                );
                break;

            case 'CONVERTING':
                return (
                    <div className="convertingItem">
                        {this.props.data.name}
                    </div>
                );
            case 'AVAILABLE':
                return(
                    <div className="availableItem">
                        <a href={this.props.data.link}>{this.props.data.name}</a>
                    </div>
                );
            break;
        }
    }
});

React.render( <LinkBox /> , document.getElementById('content'));
